export default {
  name: 'myFlowCenter',
  props: {
    process: {
      default: function () {
        return [];
      },
      type: Array
    }
  },
  computed: {
    selectModel(){

    }
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    splitName(value) {
      let arr = value.split(",");
      let html = "";
      for (let i = 0; i < arr.length; i++){
        let arrObj = arr[i].split("@*@");
        html += arrObj[1];
        if (i != arr.length - 1){
          html += ",";
        }
      }
      return html;
    }
  }
}
